html,
body,
#root,
.App {
    height: 100%;
    /*background: #293038;*/
    background-image: linear-gradient(#293038, #11231c);
    font-family: 'Noto Sans', sans-serif;
    color: white;
    overflow: hidden;
    
}

.App {
    text-align: center;
}

.container {
    height: calc(100% - 71px);
}

h1,
h2,
hr,
.nav-link,
nav,
p {
    margin: 0;
    padding: 0;
}

nav {
    justify-content: space-between;
    padding: 20px 0;
    background: #1f242a;
}

.nav-link {
    text-decoration: none;
    padding: 0 20px;
    color: white;
    font-weight: 800;
    font-size: 15px;
}

.nav-link:hover,
.nav-link:focus {
    color: #3bd6c6;
}

.selected {
    color: #b3ecec;
    font-size: 16px;
}

#navHR {
    color: #3b7760;
    border: 1px solid #89ecda;
    margin-bottom: 30px;
}

/**************************
******** CONSTRUCTION BANNER ********      
**************************/
#construction {
  position: absolute;
  top: 50px;
  left: -180px;
  width: 500px;
  transform: rotate(-40deg);
  background: #fce101;
  padding: 0 50px;
  /*background-image: url("yellow_black.png");*/
  border: 2px black solid;
  color: black;
}


/**************************
******** HOME PAGE ********      
**************************/
.home-container {
    width: 700px;
    margin: 0 auto;
    text-align: left;
    position: relative;
    top: 50%;
    transform: translateY(-100%);
    font-family: 'Source Code Pro', monospace;
}

#name-title {
    font-size: 40px;
    color: #ffff5a;
}

.variable {
    color: #dd3a93;
    font-size: 30px;
}

.plain {
    color: white;
    font-size: 28px;
}

.name-object {
    font-size: 20px;
    font-weight: bold;
    padding-top: 5px;
    position: relative;
    left: 10%;
    color: #3b7760;
}

.name-object-category {
    color: #8c75b0;
    font-size: 28px;
}

.Typewriter__cursor {
    display: none;
}

#typewriter-cursor {
    font-size: 35px;
    -webkit-animation: 1s blink step-end infinite;
    -moz-animation: 1s blink step-end infinite;
    -ms-animation: 1s blink step-end infinite;
    -o-animation: 1s blink step-end infinite;
    animation: 1s blink step-end infinite;
}

@keyframes "blink" {

    from,
    to {
        color: transparent;
    }

    50% {
        color: white;
    }
}

@-moz-keyframes blink {

    from,
    to {
        color: transparent;
    }

    50% {
        color: white;
    }
}

@-webkit-keyframes "blink" {

    from,
    to {
        color: transparent;
    }

    50% {
        color: white;
    }
}

@-ms-keyframes "blink" {

    from,
    to {
        color: transparent;
    }

    50% {
        color: white;
    }
}

@-o-keyframes "blink" {

    from,
    to {
        color: transparent;
    }

    50% {
        color: white;
    }
}

/**************************
****** CONTACT PAGE ******      
**************************/
#contact-container {
    width: 500px;
    margin: 0 auto;
    border-radius: 20px;
    border: 2px solid black;
    background: #23303c;
    padding: 20px 0;
}

#contact-form {
    margin: 35px auto;
    width: 100%;
    overflow: hidden;

}

.form-content {
    text-align: left;
    display: block;
    margin-left: 8.1%;
}

.form-input {
    width: 90%;
    background-color: #414250;
    border: 0;
    border-radius: 5px;
    margin-top: 5px;
    color: white;
    font-family: 'Noto Sans', sans-serif;
}

.form-input:invalid, #form-textarea:invalid {
    border: 1px #ff9999 solid;   
}

.form-HR {
    margin: 15px 0;
    color: #405164;
}

#form-textarea {
  resize: none;
  height: 150px;
  width: 90%;
  font-family: 'Noto Sans', sans-serif;
  background-color: #414250;
  color: white;
}

.form-required {
  color: #1db98c;
}

#form-submit {
  width: 85%;
  background-color: #1db98c;
  color: white;
  padding: 10px 0;
  margin: 10px 0;
  border: none;
  border-radius: 4px;
  cursor: pointer;
}

/* *************************
****** WORK PAGE ******      
************************* */
.work-container {
  width: 600px;
  margin: 0 auto;
}

.work-container ul {
  text-align: left;
}

.work-link:link, .work-link:visited {
  color: teal;
}

.work-link:hover, .work-link:focus {
  color: lightblue      ;
}

.this {
  color: orange;
}

.work-container hr {
  margin-top: 10px;
  margin-bottom: 20px;
}